import { ActionType, SET_TOKEN, SET_USER } from "../../type/Types";

const initialState = {
    userInfo: {},
    token: '',
    menu: [],
    routes: [],
    count: 0
}
function reducer(state = initialState, action: ActionType) {
    switch (action.type) {
        case SET_USER:
            return { ...state, userInfo: action.payload };
        case SET_TOKEN:
            return { ...state, token: action.payload };
        case 'ADD_NUM':
            console.log('action')
            console.log(action)
            return {
                ...state,
                count: state.count + action.payload.num
            }
        case 'REDUCE_NUM':
            console.log(state)
            return {
                ...state,
                count: state.count - action.payload.num
            }

        default:
            console.log(state)
            return state;
    }
}

export default reducer;
