export default class LogginView extends Component {

    // 
    onFinish = (values: any) => {
        // dispatch
        // dispatch(login({name, password}))
        login();
    };

    // 
    onFinishFailed = (errorInfo: any) => {
        console.log('Failed:', errorInfo);
    };




  render() {

    return (
        <div className='loginBox'>
            <h2><span className='primary'></span></h2>
            <Form
                name="basic"
                style={{ maxWidth: 600 }}
                initialValues={{ remember: true }}
                onFinish={this.onFinish}
                onFinishFailed={this.onFinishFailed}
                autoComplete="off"
            >
                <Form.Item
                    name="username"
                    rules={[{ required: true, message: 'Please input your username!' }]}
                >
                    <Input placeholder=':'  onChange={(e) => console.log(e.target.value)} />
                </Form.Item>

                <Form.Item
                    name="password"
                    rules={[{ required: true, message: 'Please input your password!' }]}
                >
                    <Input.Password placeholder=':'  onChange={(e) => console.log(e.target.value)} />
                </Form.Item>

                <Form.Item>
                    <Button type="primary" htmlType="submit" block>
                        
                    </Button>
                </Form.Item>
            </Form>
        </div>
    )
  }
}

